import PropTypes from "prop-types";
import { Smile } from "lucide-react";
import FuturisticDropdown from "./FuturisticDropdown";

const EMOJIS = [
  "🚀",
  "💡",
  "🤖",
  "⚡",
  "✨",
  "🌌",
  "🛰️",
  "🛸",
  "👽",
  "🧬",
  "🧪",
  "🧑‍🚀",
];

const FuturisticEmojiPicker = ({ applyStyle }) => (
  <FuturisticDropdown title="Insert Emoji" icon={<Smile size={18} />}>
    <div className="futuristic-emoji-picker">
      {EMOJIS.map((emoji) => (
        <button
          key={emoji}
          type="button"
          className="futuristic-emoji"
          onClick={() => applyStyle("insertText", emoji)}
        >
          {emoji}
        </button>
      ))}
    </div>
  </FuturisticDropdown>
);

FuturisticEmojiPicker.propTypes = {
  applyStyle: PropTypes.func.isRequired,
};

export default FuturisticEmojiPicker;
